	module.exports = {"data":{"site":{"siteMetadata":{"title":"MAJAC Blog","author":"majac6"}},"markdownRemark":{"id":"/Users/MAJAC/Documents/Project/majac/majac6.github.io/src/posts/2017-10-30-start-github-blog/index.md absPath of file >>> MarkdownRemark","html":"<p>안녕하세요. 마작입니다.\n첫 포스트는 github 페이지를 gatsby.js 로 시작하게 된 이유와 그 후기를 공유하고자 작성합니다.</p>\n<p>아마 github 페이지라면 대부분 jekyll 를 사용해 제작하는것을 가장 보편적으로 생각하실텐데요. 저도 처음엔 이 툴을 사용해 페이지를 빌드해서 사용했었고, 점점 활용범위를 넓혀가기 위해 블로그로 활용하려고 했었습니다.</p>\n<p>그러다 문득 Ruby로 작성된 jekyll 말고 nodejs 기반으로 만들어진 다른 툴이 없을까 찾아보았습니다.\n그중에 꽤 괜찮아보인 Hexo 를 발견했고, 이 툴을 이용해서 ejs 로 작성된 기본 템플릿을 pug(jade)로 바꾸고, Bootstrap 을 붙여보는 식으로 해서 작성을 해봤었지만 딱히 큰 매력을 느끼기는 어려웠는데요.</p>\n<p>개인적으로는 최신 프론트앤드 기술(Angular, React, Vue)과 연관되어있고, 정적 페이지를 빠르게 만들기 효과적인 도구를 찾고 있었거든요. 아무래도 매번 작은 홍보용 페이지를 만들기 위해 무거운 npm 모듈들을 올려가는것들을 딱히 좋아하지도 않았구요. 무엇보다 마크업언어로 된 파일들로 바로바로 포스팅도 할수 있어야 데이터베이스를 만들지 않고도 글을 올릴 필요성도 있어야 한다고 생각했습니다.</p>\n<p>그러다 다른분들의 github 페이지를 보고 gatsby.js 로 만들어진곳이 있어 공홈에 들어가보니 생각보다 Document 도 잘 정리되어 있고(개인적으로 Hexo는 Document가 빈약하다고 생각함) 최신 기술중 하나인 React 로 되어 있어서 해보고 싶다는 생각이 많이 들었습니다.</p>\n<p>아직까지 회사일+외주일 그리고 포스팅까지 하기엔 부족한 점이 많지만,\nGithub 페이지 포스팅을 통해, 제가 작업했던 내용을 다시 살펴보고 많은 분들께 공유하는 차원에서도 꽤 괜찮아 보이게 만들고 싶었는데요.</p>\n<p>이 글을 읽으시는 분들을 위해서 제가 이 페이지를 어떤 과정으로 만들었는지 소개해 드리려고 합니다.</p>","frontmatter":{"title":"gatsbyJS 로 블로그 시작하기","date":"October 30, 2017"}}},"pathContext":{"path":"/start-github-blog/"}}

